import './flow-template.scss'
import React, {useState, useRef, useEffect} from 'react';
import {Wizard, WizardStep} from '@patternfly/react-core/dist/esm/components/Wizard';
import {idpResponseData} from "../CommonInterfaces/idp-response-types";
import {UserChoices} from '../CommonInterfaces/user-choices'
import {InputValidation} from '../CommonInterfaces/input-validation'
import {PageHeader, PageHeaderTitle} from '@redhat-cloud-services/frontend-components/PageHeader';
import CustomerInformation from '../FirstStep/customer-information';
import OIDCConfig from '../SecondStep/oidc-configuration';
import SAMLConfig from '../SecondStep/saml-configuration';
import ImportFromFile from '../ThirdStep/import-from-file';
import ImportFromUrl from '../ThirdStep/import-from-url';
import ImportSamlFromFile from '../ThirdStep/import-saml-from-file';
import ImportSamlFromUrl from '../ThirdStep/import-saml-from-url';
import ImportSamlManual from '../ThirdStep/import-saml-manual';
import URLsForUser from '../FinalStep/urls-for-user';
import ConfiguredIdp from '../Configured/configured-idp';
import IdpNotSetup from "../IdpNotSetup/idp-not-setup";
import GenericFooter from './generic-footer';
import SubmitFooter from './submit-footer';
import FinalizeFooter from './finalize-footer';
import PropTypes from "prop-types";
import {getSSOApiUrl} from "../../../Utils/general-utils";
import {useChrome} from "@redhat-cloud-services/frontend-components/useChrome";
import {PageSection, Text, TextContent, TextVariants, Spinner} from "@patternfly/react-core";
import {useFlag} from "@unleash/proxy-client-react";
import {FlowStep} from "../../../Apps/IdpSelfService/Constants/flow-step";
import ValidationContext, {
    ValidationContextType
} from "../../../Apps/IdpSelfService/CommonInterfaces/validation-context";

interface Props {
    accessToken: string
}

// eslint-disable-next-line max-statements
const FlowTemplate = ({accessToken}: Props) => {

    /**
     * TODO
     *  1) Update environment URLs so that they are dynamic (mirror what 2fa app is doing? do it different?)
     *      See useChrome() method (what does this output?) - Zach's pref to look into
     *  2) Review existing mechanisms (are there vars we can rename / delete? etc)
     *      Is FlowTemplate.propTypes the only/best way to provide access to component vars?
     *  3) Unit tests / tsx file testing
     *  4) Disabled / not setup page/case handling
     *  5) How will validation rules work? Does the FE mirror the rules on the BE or just show messages if validation errors occur? Maybe FE can create an error page that displays such errors in a nice, non-error seeming way
     */
        // intended to serve as the main aggregate of data that gets sent to the server
    const userChoices = useRef<UserChoices>({selections: {}});

    // Is the admin's account been configured with an idp. If so display the management screen.
    // eslint-disable-next-line no-unused-vars
    const [isConfigured, setIsConfigured] = useState(false);
    const [isAllowedToConfigure, setIsAllowedToConfigure] = useState(false);
    const [isLoading, setIsLoading] = useState(true);
    const [isInit, setIsInit] = useState(true);
    const apiUrl = getSSOApiUrl(useChrome(), 'apis/organizations/v1/identity-providers');
    const [idpData, setIdpData] = useState({idpAlias: '', providerId: '', displayName: '', enabled: ''});
    const isWizardFlowEnabled = useFlag('iamconsole.IDPIntegration.wizardFlowEnabled')

    useEffect(() => {
        fetch(apiUrl, {
            method: 'GET',
            referrerPolicy: 'no-referrer',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + accessToken
            }
        })
            .then(response => response.json())
            .then((data: idpResponseData) => {
                if (data.providerId === 'oidc-improved' || data.providerId === 'saml-improved') {
                    setIdpData(data);
                    setIsLoading(false);
                    setIsConfigured(true);
                } else if (data.error) {
                    setIsLoading(false);
                    setIsConfigured(false);
                    setIsAllowedToConfigure(false);
                } else {
                    setIsLoading(false);
                    setIsConfigured(false);
                    setIsAllowedToConfigure(true);
                }
            })
            .catch(error => {
                console.error('Error: ', error);
                setIsLoading(false);
            })
    }, [])

    const defaultValue: ValidationContextType = {
        validations: {
            CUSTOMER_INFORMATION: {},
            OIDC_CONFIG: {},
            SAML_CONFIG: {},
            IMPORT_FROM_FILE: {},
            IMPORT_FROM_URL: {},
            IMPORT_SAML_FROM_FILE: {},
            IMPORT_SAML_FROM_URL: {},
            IMPORT_SAML_MANUALLY: {}
        },
        // eslint-disable-next-line no-empty-function
        setValidations: () => {
        }
    };

    // handle the present validation state of the various steps.
    const [validations, setValidations] = useState<InputValidation>(defaultValue.validations)

    /**
     * This object is intended to keep a map of the current flow state including next state and
     * previous state when available.
     */
    const [flowState, setFlowState] = useState({
        currentState: FlowStep.CUSTOMER_INFORMATION,
        previousState: ""
    })

    /**
     * seems to be a function required by default for wizard. test at later date to verify. If not then remove. Gets executed
     * when the wizard isn't otherwise specified what to do.
     */
        // eslint-disable-next-line no-empty-function
    const closeWizard = () => {
        }

    // manages what flow states are to be displayed from within the wizard
    const [displayed, setDisplayed] = useState({
        hideCustomerInformation: false,
        hideOIDC: true,
        hideSAML: true,
        hideImportFromURL: true,
        hideImportFromFile: true,
        hideImportManually: true,
        hideFinish: true,
    })

    /*
     * used to communicated between the footer and currently displayed window.
     * this is currently unused, but I suggest leaving this here until we have come up with a fix for displaying the
     * error state
     */
    // eslint-disable-next-line no-unused-vars
    const [nextTriggered, setNextTriggered] = useState(false);

    const triggerNextStep = (nextStep: string) => {
        // Need to pull the current state, run checks, and
        let newDisplay;

        switch (nextStep) {
            case FlowStep.OIDC_CONFIG:
                newDisplay = {...displayed};
                newDisplay.hideOIDC = false;
                newDisplay.hideSAML = true;
                setDisplayed(newDisplay);
                setFlowState({
                    currentState: nextStep,
                    previousState: FlowStep.CUSTOMER_INFORMATION
                });
                break;
            case FlowStep.SAML_CONFIG:
                newDisplay = {...displayed};
                newDisplay.hideSAML = false;
                newDisplay.hideOIDC = true;
                setDisplayed(newDisplay);
                setFlowState({
                    currentState: nextStep,
                    previousState: FlowStep.CUSTOMER_INFORMATION
                });
                break;
            case FlowStep.IMPORT_FROM_FILE:
                newDisplay = {...displayed};
                newDisplay.hideImportFromFile = false;
                setDisplayed(newDisplay);
                setFlowState({
                    currentState: nextStep,
                    previousState: FlowStep.OIDC_CONFIG
                });
                break;
            case FlowStep.IMPORT_FROM_URL:
                newDisplay = {...displayed};
                newDisplay.hideImportFromURL = false;
                setDisplayed(newDisplay);
                setFlowState({
                    currentState: nextStep,
                    previousState: FlowStep.OIDC_CONFIG
                });
                break;
            case FlowStep.IMPORT_SAML_FROM_FILE:
                newDisplay = {...displayed};
                newDisplay.hideImportFromFile = false;
                setDisplayed(newDisplay);
                setFlowState({
                    currentState: nextStep,
                    previousState: FlowStep.SAML_CONFIG
                });
                break;
            case FlowStep.IMPORT_SAML_MANUALLY:
                newDisplay = {...displayed};
                newDisplay.hideImportManually = false;
                setDisplayed(newDisplay);
                setFlowState({
                    currentState: nextStep,
                    previousState: FlowStep.SAML_CONFIG
                });
                break;
            case FlowStep.IMPORT_SAML_FROM_URL:
                newDisplay = {...displayed};
                newDisplay.hideImportFromURL = false;
                setDisplayed(newDisplay);
                setFlowState({
                    currentState: nextStep,
                    previousState: FlowStep.SAML_CONFIG
                });
                break;
            case FlowStep.FINAL_STEP:
                newDisplay = {...displayed};
                newDisplay.hideFinish = false;
                setDisplayed(newDisplay);
                setFlowState({
                    currentState: nextStep,
                    previousState: flowState.currentState,
                });
                break;
            default:
                setDisplayed({
                    hideCustomerInformation: false,
                    hideOIDC: true,
                    hideSAML: true,
                    hideImportFromURL: true,
                    hideImportFromFile: true,
                    hideImportManually: true,
                    hideFinish: true,
                });

        }

    }

    /**
     *
     * @param validChoices an array of strings representing the values that will remain in the selection list.
     */
    const pruneChoices = (validChoices: string[]) => {
        const choicesKeys = Object.keys(userChoices.current.selections);
        for (let i = 0; i < choicesKeys.length; i++) {
            if (!validChoices.includes(choicesKeys[i])) {
                // @ts-ignore
                delete userChoices.current.selections[choicesKeys[i]];
            }
        }
    }

    const triggerBackStep = (prevStep: string) => {
        switch (prevStep) {
            case FlowStep.OIDC_CONFIG:
                pruneChoices(["protocol"]);
                setFlowState({
                    currentState: prevStep,
                    previousState: FlowStep.CUSTOMER_INFORMATION,
                });
                setDisplayed({
                    hideCustomerInformation: false,
                    hideOIDC: false,
                    hideSAML: true,
                    hideImportFromURL: true,
                    hideImportFromFile: true,
                    hideImportManually: true,
                    hideFinish: true,
                })
                break;
            case FlowStep.SAML_CONFIG:
                pruneChoices(["protocol"]);
                setFlowState({
                    currentState: prevStep,
                    previousState: FlowStep.CUSTOMER_INFORMATION,
                });
                setDisplayed({
                    hideCustomerInformation: false,
                    hideOIDC: true,
                    hideSAML: false,
                    hideImportFromURL: true,
                    hideImportFromFile: true,
                    hideImportManually: true,
                    hideFinish: true,
                })
                break;
            case FlowStep.IMPORT_FROM_FILE:
                pruneChoices(["clientId", "clientSecret", "inputMethod", "protocol"]);
                setFlowState({
                    currentState: prevStep,
                    previousState: FlowStep.OIDC_CONFIG,
                });
                setDisplayed({
                    hideCustomerInformation: false,
                    hideOIDC: false,
                    hideSAML: true,
                    hideImportFromURL: true,
                    hideImportFromFile: false,
                    hideImportManually: true,
                    hideFinish: true,
                })
                break;
            case FlowStep.IMPORT_FROM_URL:
                pruneChoices(["clientId", "clientSecret", "inputMethod", "protocol"]);
                setFlowState({
                    currentState: prevStep,
                    previousState: FlowStep.OIDC_CONFIG,
                });
                setDisplayed({
                    hideCustomerInformation: false,
                    hideOIDC: false,
                    hideSAML: true,
                    hideImportFromURL: false,
                    hideImportFromFile: true,
                    hideImportManually: true,
                    hideFinish: true,
                })
                break;
            case FlowStep.IMPORT_SAML_FROM_URL:
                pruneChoices(["clientId", "clientSecret", "inputMethod", "protocol"]);
                setFlowState({
                    currentState: prevStep,
                    previousState: FlowStep.SAML_CONFIG,
                });
                setDisplayed({
                    hideCustomerInformation: false,
                    hideOIDC: true,
                    hideSAML: false,
                    hideImportFromURL: false,
                    hideImportFromFile: true,
                    hideImportManually: false,
                    hideFinish: true,
                })
                break;
            case FlowStep.IMPORT_SAML_MANUALLY:
                pruneChoices(["clientId", "clientSecret", "inputMethod", "protocol"]);
                setFlowState({
                    currentState: prevStep,
                    previousState: FlowStep.SAML_CONFIG,
                });
                setDisplayed({
                    hideCustomerInformation: false,
                    hideOIDC: true,
                    hideSAML: false,
                    hideImportFromURL: true,
                    hideImportFromFile: true,
                    hideImportManually: false,
                    hideFinish: true,
                })
                break;
            case FlowStep.IMPORT_SAML_FROM_FILE:
                pruneChoices(["clientId", "clientSecret", "inputMethod", "protocol"]);
                setFlowState({
                    currentState: prevStep,
                    previousState: FlowStep.SAML_CONFIG,
                });
                setDisplayed({
                    hideCustomerInformation: false,
                    hideOIDC: true,
                    hideSAML: false,
                    hideImportFromURL: true,
                    hideImportFromFile: false,
                    hideImportManually: true,
                    hideFinish: true,
                })
                break;
            default:
                pruneChoices([""]);
                setDisplayed({
                    hideCustomerInformation: false,
                    hideOIDC: true,
                    hideSAML: true,
                    hideImportFromURL: true,
                    hideImportFromFile: true,
                    hideImportManually: true,
                    hideFinish: true,
                })
        }
    }

    const setBackTriggered = () => {
        triggerBackStep(flowState.previousState)
    }

    return (
        <div className="flowPage">
            <PageHeader className="wizardHeader">
                <PageHeaderTitle title="Identity Provider Integration" className="pf-v5-u-mb-lg"/>
                <TextContent>
                    Manage identity provider integration for your organization.{' '}
                    <Text component={TextVariants.a}>
                        {/* TODO: add the correct URL below
                         <Button variant="link" href={"https://www.redhat.com"} target="_blank" component="a" isInline icon={<ExternalLinkSquareAltIcon />} iconPosition="right" className="pf-v5-u-ml-xs">
                            Learn more about identity provider integration
                        </Button>
                         */}
                    </Text>
                </TextContent>
            </PageHeader>
            <PageSection>
                <div className={isLoading ? 'loadingIcon' : 'hiddenPage'}>
                    <Spinner/>
                </div>
                <div className={!isLoading && isConfigured ? '' : 'hiddenPage'}>
                    <ConfiguredIdp responseData={idpData} milestoneTwoFeaturesEnabled={isWizardFlowEnabled}/>
                </div>
                <div className={(!isLoading && !isConfigured && isInit) ? 'initPage' : 'hiddenPage'}>
                    <IdpNotSetup setupIDP={() => {
                        setIsInit(false)
                    }} wizardEnabled={isWizardFlowEnabled && isAllowedToConfigure}/>
                </div>
                <div className={(!isConfigured && !isInit) ? '' : 'hiddenPage'}>
                    <ValidationContext.Provider value={{validations, setValidations}}>
                        <Wizard
                            onClose={closeWizard}
                            isProgressive>

                            {/* Standard customer config. Every flow requires this */}
                            <WizardStep
                                id={'CUSTOMERINFORMATION'}
                                name={'Customer Information'}
                                isHidden={displayed.hideCustomerInformation}
                                footer={
                                    <GenericFooter
                                        userChoices={userChoices.current}
                                        setNextTriggered={setNextTriggered}
                                        setPreviousTriggered={setBackTriggered}
                                        currentState={'CUSTOMER_INFORMATION'}
                                    />
                                }>
                                <CustomerInformation
                                    resetNextClicked={setNextTriggered}
                                    rootUserChoices={userChoices.current}
                                    updateStatus={triggerNextStep}
                                />
                            </WizardStep>

                            {/* OIDC Config. Only run through if a customer chooses OIDC configuration */}
                            <WizardStep
                                id={'OIDCCONFIG'}
                                name={'OIDC Configuration'}
                                isHidden={displayed.hideOIDC}
                                footer={
                                    <GenericFooter
                                        userChoices={userChoices.current}
                                        setNextTriggered={setNextTriggered}
                                        setPreviousTriggered={setBackTriggered}
                                        currentState={'OIDC_CONFIG'}
                                    />
                                }>
                                <OIDCConfig
                                    resetNextClicked={setNextTriggered}
                                    rootUserChoices={userChoices.current}
                                    updateStatus={triggerNextStep}
                                />
                            </WizardStep>

                            <WizardStep
                                id={'IMPORTFROMFILE'}
                                name={'Import config from file'}
                                isHidden={!(!displayed.hideOIDC && !displayed.hideImportFromFile)}
                                footer={
                                    <SubmitFooter
                                        userChoiceData={userChoices}
                                        previous={setBackTriggered}
                                        accessToken={accessToken}
                                        currentState={FlowStep.IMPORT_FROM_FILE}
                                    />
                                }>
                                <ImportFromFile
                                    updateStatus={triggerNextStep}
                                    resetNextClicked={setNextTriggered}
                                    rootUserChoices={userChoices.current}
                                />
                            </WizardStep>

                            <WizardStep
                                id={'IMPORTFROMURL'}
                                name={'Import config from URL'}
                                isHidden={!(!displayed.hideOIDC && !displayed.hideImportFromURL)}
                                footer={
                                    <SubmitFooter
                                        userChoiceData={userChoices}
                                        previous={setBackTriggered}
                                        accessToken={accessToken}
                                        currentState={FlowStep.IMPORT_FROM_URL}
                                    />
                                }>
                                <ImportFromUrl
                                    resetNextClicked={setNextTriggered}
                                    rootUserChoices={userChoices}
                                />
                            </WizardStep>

                            {/* SAML Config. Only run through if the customer chooses a SAML configuration */}
                            <WizardStep
                                id={'SAMLCONFIG'}
                                name={'Saml Configuration'}
                                isHidden={displayed.hideSAML}
                                footer={
                                    <GenericFooter
                                        userChoices={userChoices.current}
                                        setNextTriggered={setNextTriggered}
                                        setPreviousTriggered={setBackTriggered}
                                        currentState={'SAML_CONFIG'}
                                    />
                                }>
                                <SAMLConfig
                                    resetNextClicked={setNextTriggered}
                                    rootUserChoices={userChoices.current}
                                    updateStatus={triggerNextStep}
                                />
                            </WizardStep>

                            <WizardStep
                                name={'Import SAML from file'}
                                id={'IMPORTSAMLFROMFILE'}
                                isHidden={!(!displayed.hideSAML && !displayed.hideImportFromFile)}
                                footer={
                                    <SubmitFooter
                                        userChoiceData={userChoices}
                                        previous={setBackTriggered}
                                        accessToken={accessToken}
                                        currentState={FlowStep.IMPORT_SAML_FROM_FILE}
                                    />
                                }>
                                <ImportSamlFromFile
                                    resetNextClicked={setNextTriggered}
                                    rootUserChoices={userChoices.current}
                                    updateStatus={triggerNextStep}
                                />

                            </WizardStep>
                            <WizardStep
                                name={'Import SAML Manually'}
                                id={'IMPORTSAMLMANUALLY'}
                                isHidden={!(!displayed.hideSAML && !displayed.hideImportManually)}
                                footer={
                                    <SubmitFooter
                                        userChoiceData={userChoices}
                                        previous={setBackTriggered}
                                        accessToken={accessToken}
                                        currentState={FlowStep.IMPORT_SAML_MANUALLY}
                                    />
                                }>

                                <ImportSamlManual
                                    updateStatus={triggerNextStep}
                                    resetNextClicked={setNextTriggered}
                                    rootUserChoices={userChoices.current}
                                />

                            </WizardStep>

                            <WizardStep
                                id={'IMPORTSAMLFROMURL'}
                                name={'Import SAML from URL'}
                                isHidden={!(!displayed.hideSAML && !displayed.hideImportFromURL)}
                                footer={
                                    <SubmitFooter
                                        userChoiceData={userChoices}
                                        previous={setBackTriggered}
                                        accessToken={accessToken}
                                        currentState={FlowStep.IMPORT_SAML_FROM_URL}
                                    />
                                }>

                                <ImportSamlFromUrl
                                    resetNextClicked={setNextTriggered}
                                    rootUserChoices={userChoices}
                                />

                            </WizardStep>

                            {/* Final steps of the process. again any selection will show the following two steps.*/}
                            <WizardStep
                                id={'FINALSTEP'}
                                name={'Configure URL\'s'}
                                isHidden={displayed.hideFinish}
                                footer={
                                    <FinalizeFooter/>
                                }>
                                {/* should support both SAML and OIDC params */}
                                <URLsForUser accessToken={accessToken}/>
                            </WizardStep>
                        </Wizard>
                    </ValidationContext.Provider>
                </div>
            </PageSection>
        </div>
);
}


FlowTemplate.propTypes = {
    accessToken: PropTypes.string
}

export default FlowTemplate;
