// step two, choice 2 of the flow https://miro.com/app/board/uXjVO_x__QE=/

import {
    Radio,
    Form,
    FormGroup
} from '@patternfly/react-core';

import React, {useEffect} from 'react';
import PropTypes from "prop-types";
import {UserChoices} from "../CommonInterfaces/user-choices";
import {useValidationContext} from "../CommonInterfaces/validation-context";

interface Props{
    resetNextClicked: Function;
    rootUserChoices: UserChoices;
    updateStatus: Function;
}

const SamlConfiguration = ({resetNextClicked, rootUserChoices, updateStatus}: Props) => {

    const handleSelection = (value: any) => {

        /*
         * regardless of current state. if the user alters the input clear existing flags. This should also reset
         * the currently displayed errors
         */
        resetNextClicked(false);

        //  update the main data storage object with the new value
        rootUserChoices.selections[value.target.id] = value.target.value;

        if (rootUserChoices.selections.inputMethod) { updateStatus(rootUserChoices.selections.inputMethod); }
    }

    const { validations, setValidations } = useValidationContext();

    useEffect(() => {
        const updated = {...validations};
        updated.SAML_CONFIG =
            {
            'inputMethod': {
                inputType: 'RADIO',
                variant: 'default',
                message: ''
            }
        };
        setValidations(updated);
    }, []);

    return <Form>
                <FormGroup
                    onChange={(value) => { handleSelection(value) }}
                    label="Load external IDP metadata from a config file"
                    isRequired>
                    <Radio name="inputChoice" id="inputMethod" label="Import from file" value="IMPORT_SAML_FROM_FILE"/>
                    <Radio name="inputChoice" id="inputMethod" label="Enter Manually" value="IMPORT_SAML_MANUALLY"/>
                </FormGroup>
            </Form>
}

SamlConfiguration.propTypes = {
    resetNextClicked: PropTypes.func,
    rootUserChoices: PropTypes.object,
    updateStatus: PropTypes.func
}

export default SamlConfiguration;
