import React from 'react';
import { WizardFooterWrapper, useWizardContext} from '@patternfly/react-core/dist/esm/components/Wizard';
import { Button } from '@patternfly/react-core';
import { isFailureTriggered } from "../../../Utils/general-utils";
import {UserChoices} from "../CommonInterfaces/user-choices";
import {useValidationContext} from "../CommonInterfaces/validation-context";


interface Props {
    userChoices: UserChoices;
    setNextTriggered: Function;
    setPreviousTriggered: Function;
    currentState: string;
}

const GenericFooter = ({userChoices, setNextTriggered, setPreviousTriggered, currentState}: Props) => {
    const { goToNextStep, goToPrevStep } = useWizardContext();
    const { validations, setValidations } = useValidationContext();

    /**
     * Governs what happens when the next button gets clicked. Important for allowing the back and forth communication
     * between the footer element and the actual component being displayed when needing to perform steps such as validation.
     */
    const handleNextClick = () => {
        if (!isFailureTriggered(userChoices, currentState, validations, setValidations)) {
            goToNextStep();
        }
        else {
            setNextTriggered(true);
        }
    }

    /**
     * on back click we need to be able to trigger the wizard context's default back functionality but we also need to
     * handle the current state stored in the flowTemplate.
     */
    const handleBackClick = () => {
        // handles wizard default functionality
        goToPrevStep();
        // custom logic to update the current flow state.
        setPreviousTriggered();
    }

    return (
        <WizardFooterWrapper>
            <Button onClick={handleNextClick}>Next</Button>
            <Button variant="secondary" onClick={handleBackClick}>
                Back
            </Button>
        </WizardFooterWrapper>
    );
}

export default GenericFooter;
