import React, {createContext, useContext} from 'react';
import {InputValidation} from "../CommonInterfaces/input-validation";

export interface ValidationContextType {
    validations: InputValidation,
    setValidations: React.Dispatch<React.SetStateAction<any>>
}

export const ValidationContext = createContext<ValidationContextType>({
    validations: {
        CUSTOMER_INFORMATION: {},
        OIDC_CONFIG: {},
        SAML_CONFIG: {},
        IMPORT_FROM_FILE: {},
        IMPORT_FROM_URL: {},
        IMPORT_SAML_FROM_FILE: {},
        IMPORT_SAML_FROM_URL: {},
        IMPORT_SAML_MANUALLY: {}
    },
    // eslint-disable-next-line no-empty-function
    setValidations: () => {}
});

export const useValidationContext = () => {
    const context = useContext(ValidationContext);
    if (!context) {
        throw new Error('Validation context not found');
    }
    return context;
}

export default ValidationContext;
