import React, {useEffect, useState} from 'react';
import {
    TextInput,
    Form,
    FormGroup,
    HelperText,
    HelperTextItem, TextArea
} from '@patternfly/react-core';
import {setStateValue} from '../../../Utils/general-utils';
import {UserChoices} from "@apps/IdpSelfService/CommonInterfaces/user-choices";
import {AUTH_REQUEST_URL, IDENTITY_PROVIDER_ENTITY_ID, X509_CERTIFICATE, SERVICE_PROVIDER_ENTITY_ID} from "../../../Apps/IdpSelfService/CommonInterfaces/idp-request-types";
import {useValidationContext} from "../../../Apps/IdpSelfService/CommonInterfaces/validation-context";
import {FlowStep} from "../../../Apps/IdpSelfService/Constants/flow-step";

interface Props {
    updateStatus: Function;
    resetNextClicked: Function;
    rootUserChoices: UserChoices;
}
const ImportSamlManual = ({updateStatus, resetNextClicked, rootUserChoices}: Props) => {
    const [selections, setSelections] = useState();

    /**
     * updates the information being used to govern the application flow as well as the final api update object.
     * @param value
     */
    const handleSelection = (value: any) => {

        /*
         * regardless of current state. if the user alters the input clear existing flags. This should also reset
         * the currently displayed errors
         */
        resetNextClicked(false);

        //  update the main data storage object with the new value
        rootUserChoices.selections[value.target.id] = value.target.value;

        if (rootUserChoices.selections) { updateStatus(FlowStep.FINAL_STEP); }
    }

    const parseVariantValue = (value: string | undefined): ("default" | "error" | undefined) => {
        if (value === 'default' || value === 'error') { return value; }
        // eslint-disable-next-line
        return undefined;
    }


    const { validations, setValidations } = useValidationContext();

    useEffect(() => {
        const updated = {...validations};
        updated.IMPORT_SAML_MANUALLY =
            {
                'identityProviderEntityId': {
                    inputType: 'DOMAIN',
                    variant: 'default',
                    message: 'The entityID used to validate the Issuer for received SAML assertions.'
                },
                'serviceProviderEntityId': {
                    inputType: 'GENERAL',
                    variant: 'default',
                    message: 'The Entity ID that will be used to uniquely identify the SAML Service Provider. This might also be referred to as a "client"'
                },
                'authenticationRequestsUrl': {
                    inputType: 'DOMAIN',
                    variant: 'default',
                    message: 'The URL that sends SAML authentication requests'
                },
                'x509Certificate': {
                    inputType: 'GENERAL',
                    variant: 'default',
                    message: 'A PEM certificate that is used to check for signatures.'
                }
            };
        setValidations(updated);
    }, []);

    return <Form>
        <FormGroup
            label="Identity Provider Entity ID"
            isRequired
            onChange={(value) => { handleSelection(value) }}>
            <HelperText>
                <HelperTextItem variant={ parseVariantValue(validations.IMPORT_SAML_MANUALLY?.identityProviderEntityId?.variant) }>
                    { validations.IMPORT_SAML_MANUALLY?.identityProviderEntityId?.message }
                </HelperTextItem>
            </HelperText>
            <TextInput
                onChange={(targetElement) => { setStateValue(targetElement.currentTarget.value, IDENTITY_PROVIDER_ENTITY_ID, selections, setSelections) }}
                type="text"
                id={IDENTITY_PROVIDER_ENTITY_ID} />
        </FormGroup>

        <FormGroup
            label="Authentication Request URL"
            isRequired
            onChange={(value) => { handleSelection(value) }}>
            <HelperText>
                <HelperTextItem variant={ parseVariantValue(validations.IMPORT_SAML_MANUALLY?.authenticationRequestsUrl?.variant) }>
                    { validations.IMPORT_SAML_MANUALLY?.authenticationRequestsUrl?.message }
                </HelperTextItem>
            </HelperText>
            <TextInput onChange={(formElement) => { setStateValue(formElement.currentTarget.value, AUTH_REQUEST_URL, selections, setSelections) }}
                       type="text"
                       id={AUTH_REQUEST_URL} />
        </FormGroup>

        <FormGroup
            label="Service Provider Entity ID"
            isRequired
            onChange={(value) => { handleSelection(value) }}>
            <HelperText>
                <HelperTextItem variant={ parseVariantValue(validations.IMPORT_SAML_MANUALLY?.serviceProviderEntityId?.variant) }>
                    { validations.IMPORT_SAML_MANUALLY?.serviceProviderEntityId?.message }
                </HelperTextItem>
            </HelperText>
            <TextInput onChange={(formElement) => { setStateValue(formElement.currentTarget.value, AUTH_REQUEST_URL, selections, setSelections) }}
                       type="text"
                       id={SERVICE_PROVIDER_ENTITY_ID} />
        </FormGroup>

        <FormGroup
            label="x509 Certificate"
            isRequired
            onChange={(value) => { handleSelection(value) }}>
            <HelperText>
                <HelperTextItem variant={ parseVariantValue(validations.IMPORT_SAML_MANUALLY?.x509Certificate?.variant) }>
                    { validations.IMPORT_SAML_MANUALLY?.x509Certificate?.message }
                </HelperTextItem>
            </HelperText>
            <TextArea onChange={(formElement) => { setStateValue(formElement.currentTarget.value, X509_CERTIFICATE, selections, setSelections) }}
                      resizeOrientation="vertical"
                      type="text"
                      id={X509_CERTIFICATE}/>
        </FormGroup>

    </Form>

}

export default ImportSamlManual;
