// step two, choice 1 of the flow https://miro.com/app/board/uXjVO_x__QE=/
import React, {useEffect, useState} from 'react';
import { 
    Text,
    TextInput,
    Radio,
    Form,
    FormGroup,
    HelperText,
    HelperTextItem
} from '@patternfly/react-core';
import {setStateValue} from '../../../Utils/general-utils';
import PropTypes from 'prop-types';
import {UserChoices} from '../CommonInterfaces/user-choices'
import {
    useValidationContext
} from "../../../Apps/IdpSelfService/CommonInterfaces/validation-context";

interface Props{
    resetNextClicked: Function;
    rootUserChoices: UserChoices;
    updateStatus: Function;
}

const OidcConfiguration = ({resetNextClicked, rootUserChoices, updateStatus}: Props) => {
    const [selections, setSelections] = useState<Object>();

    /**
     * handle selection needs to update information being used to govern
     * the applicaton flow as well as the final api update object
     */
    const handleSelection = (value: any) => {

        /*
         * regardless of current state. if the user alters the input clear existing flags. This should also reset
         * the currently displayed errors
         */
        resetNextClicked(false);

        //  update the main data storage object with the new value
        rootUserChoices.selections[value.target.id] = value.target.value;

        if (rootUserChoices.selections.inputMethod) {
            updateStatus(rootUserChoices.selections.inputMethod);
        }
    }

    const parseVariantValue = (value: string | undefined): ("default" | "error" | undefined) => {
        if (value === 'default' || value === 'error') {
            return value;
        }
        // eslint-disable-next-line
        return undefined;
    }

    const { validations, setValidations } = useValidationContext();

    useEffect(() => {
        const updated = {...validations};
        updated.OIDC_CONFIG =
            {
                'clientId': {
                    inputType: 'GENERAL',
                    variant: 'default',
                    message: 'The Red Hat client identifier registered within the customer identity provider. Your identity provider generates this client ID when a Red Hat client is added'
                },
                'clientSecret': {
                    inputType: 'GENERAL',
                    variant: 'default',
                    message: 'Enter the client secret associated with your client identifier  by your identity provider'
                },
                'inputMethod': {
                    inputType: 'RADIO',
                    variant: 'default',
                    message: ''
                }
            };
        setValidations(updated);
    }, []);

    // @ts-ignore
    return <Form>
                <Text className="pf-v5-u-color-400 pf-v5-u-font-size-sm">All fields are required</Text>
                <FormGroup
                    label="Client ID/SP entity ID"
                    onChange={(value) => { handleSelection(value) }}>
                    <TextInput
                        onChange={(_event, value) => setStateValue(value, "clientId", selections, setSelections)}
                        type="text"
                        id="clientId"
                    />
                    <HelperText>
                        <HelperTextItem variant={ parseVariantValue(validations.OIDC_CONFIG?.clientId?.variant) }>
                            { validations.OIDC_CONFIG?.clientId?.message }
                        </HelperTextItem>
                    </HelperText>
                </FormGroup>

                <FormGroup
                    label="Client Secret"
                    onChange={(value) => { handleSelection(value) }}>
                    <TextInput
                        onChange={(_event, value) => setStateValue(value, "clientSecret", selections, setSelections)}
                        type="text"
                        id="clientSecret"
                    />
                    <HelperText>
                        <HelperTextItem variant={parseVariantValue(validations.OIDC_CONFIG?.clientSecret?.variant)}>
                            { validations.OIDC_CONFIG?.clientSecret?.message }
                        </HelperTextItem>
                    </HelperText>
                </FormGroup>

                <FormGroup
                    onChange={(value) => { handleSelection(value) }}
                    label="Load external IDP metadata from a config file or download it from a url">
                    <Radio name="inputChoice" id="inputMethod" label="Import from URL" value="IMPORT_FROM_URL"/>
                    <Radio name="inputChoice" id="inputMethod" label="Import from file" value="IMPORT_FROM_FILE"/>
                    <HelperText>
                        <HelperTextItem variant={parseVariantValue(validations.OIDC_CONFIG?.inputMethod?.variant)}>
                            { validations.OIDC_CONFIG?.inputMethod?.message }
                        </HelperTextItem>
                    </HelperText>
                </FormGroup>
            </Form>
}

OidcConfiguration.propTypes = {
    resetNextClicked: PropTypes.func,
    rootUserChoices: PropTypes.object
}

export default OidcConfiguration;
